  color: darkgrey; }

.container {
  padding: 16px; }

.signupbox {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: transparent;
  padding-top: 50px; }

.signupbox-content {
  background-color: #fefefe;
  margin: 5% auto 15% auto;
  /* 5% from the top, 15% from the bottom and centered */
  border: 1px solid #fff;
  width: 80%;
  /* Could be more or less, depending on screen size */
  height: 80%; }

hr {
